/* eslint-disable react/no-children-prop */
import React from "react";
import { render } from "@testing-library/react";
import WrapperContext from "../../mocks/wrappercontext";
import GenericModal from "./GenericModal";

describe("GenericModal", () => {
    it("GenericModal render test", () => {
        const fcClose = jest.fn()

        const {getByTestId} = render(<WrapperContext>
            <GenericModal
                testId="modal-generic-component"
                isOpen
                children={<h1>test modal</h1>}
                onClose={fcClose}
            />
        </WrapperContext>)

        const modal = getByTestId("modal-generic-component")
        const clone = getByTestId("close")
        clone.click();

        expect(modal).toBeTruthy();
        expect(fcClose).toBeCalled();
    });

    it("GenericModal render test with close not function param", () => {
        const { getByTestId } = render(<WrapperContext>
            <GenericModal
                testId="modal-generic-component"
                isOpen
                children={<h1>test modal</h1>}
            />
        </WrapperContext>)

        const modal = getByTestId("modal-generic-component")
        const clone = getByTestId("close")
        clone.click();

        expect(modal).toBeTruthy();
    });
})
